.modalSignup__form {
  width: calc(100% - 30px);
  max-width: 450px;
  height: auto;
  background-color: #84a178;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.backdrop {
  opacity: 1;
  width: 100%;
  height: 100%;
  z-index: 100;
  visibility: visible;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop .is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-location {
  visibility: hidden;
  width: 280px;
  height: 365px;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 7px;
  padding: 28px;
  display: block;
  position: relative;
}

.modal-location__header {
  text-align: center;
  margin: 0 auto 24px;
}

.modal-location__button {
  width: 134px;
