  border-radius: 10px;
  margin: 0 auto;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 600;
}

.modal-location__button:hover {
  background-color: #0a8817;
}

.modal-location__button:focus {
  color: #fcfcfc;
  fill: #fcfcfc;
  background-color: #0a8817;
}

.modal-location__map {
